import React, { Component } from 'react'
import { Accordion, Menu, Image, Segment } from 'semantic-ui-react'

const menuHeaderStyle = {
  backgroundColor: 'black',
  color: 'white',
  textAlign: 'center',
  fontStyle: 'italic'
}

export default class MenuVert extends Component {
  state = { activeItem: 'Bio' }
  state = { activeIndex: '' }

  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll, false)
  }

  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name });
    let element = document.getElementsByClassName(name)[0];
    element.scrollIntoView({behavior: 'smooth'});
  }

  handleAccordionClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  handleScroll = () => {
    let introNode = document.getElementsByClassName('Intro')[0]
    let animalNode = document.getElementsByClassName('Animals')[0]
    let programmingNode = document.getElementsByClassName('Programming')[0]
    let projectsNode = document.getElementsByClassName('Projects')[0]
    let connectNode = document.getElementsByClassName('Connect')[0]
    let menuArray = [ introNode, animalNode, programmingNode, projectsNode, connectNode]

    menuArray.map(e => {
      e.scrollTop == window.scrollY ? this.setState({ activeItem: e.className }) : null;
    })

    //let elementScroll = document.getElementsByClassName(name)[0].scrollTop
    //if(currentScroll == elementScroll) {
    //  this.setState({ activeItem: name });
    //}
  }

  render() {
    const { activeItem } = this.state
    const { activeIndex } = this.state

    const BioContent = (
      <Menu.Menu>
        <Menu.Item
          name='Intro'
          active={activeItem === 'Intro'}
          onClick={this.handleItemClick}
        />
        <Menu.Item
          name='Animals'
          active={activeItem === 'Animals'}
          onClick={this.handleItemClick}
        />
        <Menu.Item
          name='Programming'
          active={activeItem === 'Programming'}
          onClick={this.handleItemClick}
        />
      </Menu.Menu>
    )

    return (
      <div>
        <Segment style={menuHeaderStyle}>
          <Image src='/web-portfolio-png/name.png' size='small' centered/>
          <p>( Hwang, Jun-Hyun )</p>
        </Segment>
        
        <Accordion as={Menu} pointing secondary vertical fluid inverted>
            <Accordion.Title
              as={Menu.Item}
              content='Bio'
              active={activeIndex === 0}
              index={0}
              onClick={this.handleAccordionClick}
            />
            <Accordion.Content active={activeIndex === 0} content={BioContent} />
          <Menu.Item
            name='Projects'
            active={activeItem === 'Projects'}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name='Connect'
            active={activeItem === 'Connect'}
            onClick={this.handleItemClick}
          />
        </Accordion>
        <style jsx global>
          {`
            .ui.secondary.vertical.pointing.menu .item {
              border-left-style: solid;
              border-left-width: 2px;
              border-left-color: grey;
              border-right-style: none;
              border-right-width: 0;
              border-right-color: none;
            }
            .ui.secondary.vertical.pointing.menu {
              border-left-width: 2px;
              border-left-style: solid;
              border-left-color: rgba(34,36,38,.15);
              border-right-width: 0;
              border-right-style: none;
              border-right-color: none;
            }
            .ui.secondary.vertical.pointing.menu .item {
              margin: 0 0 0 -2px;
            }
            .ui.vertical.menu .item>i.icon {
              transform: scaleX(-1);
            }
            .ui.accordion:not(.styled) .accordion .title~.content:not(.ui), .ui.accordion:not(.styled) .title~.content:not(.ui) {
              padding: 0;
              text-indent: 18px;
            }
          `}
        </style>
      </div>
    )
  }
}